import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-order-details',
  templateUrl: './order-details.component.html',
  styleUrls: ['./order-details.component.scss']
})
export class OrderDetailsComponent implements OnInit {
  orderId;
  constructor(private route: ActivatedRoute) { }

  ngOnInit() {
    this.route.queryParamMap.subscribe(params => {
      this.orderId = params['params'].orderId;
    })
  }
  alternate: boolean = true;
  toggle: boolean = true;
  color: boolean = false;
  size: number = 20;
  expandEnabled: boolean = true;
  side = 'left';

  entries = [
    {
      header: 'Order Placed to Vendor',
      content: 'content',

    },
    {
      header: 'Order Received from Manufacturer',
      content: 'content'
    },
    {
      header: 'Order Placed to Sub-Vendor',
      content: 'content'
    },
    {
      header: 'Order Received from Vendor',
      content: 'content'
    }
    , {
      header: 'Order Received from Sub-Vendor',
      content: 'content'
    }
    , {
      header: 'Order Shipped to Vendor',
      content: 'content'
    }
    , {
      header: 'Order Completed from Sub-Vendor',
      content: 'content'
    }
    , {
      header: 'Order Received from Vendor',
      content: 'content'
    }
    , {
      header: 'Order Shipped to Manufacturer',
      content: 'content'
    }
    , {
      header: 'Order Completed from Vendor',
      content: 'content'
    }
  ]

  addEntry() {
    this.entries.push({
      header: 'header',
      content: 'content'
    })
  }

  removeEntry() {
    this.entries.pop();
  }

  onHeaderClick(event) {
    if (!this.expandEnabled) {
      event.stopPropagation();
    }
  }

  onDotClick(event) {
    if (!this.expandEnabled) {
      event.stopPropagation();
    }
  }

  onExpandEntry(expanded, index) {
  }



  toggleSide() {
    this.side = this.side === 'left' ? 'right' : 'left';
  }

}
