import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { OrderService } from 'src/app/core/services/order.service';
import { SnackBarService } from 'src/app/shared-module/services/snack-bar.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-new-order',
  templateUrl: './new-order.component.html',
  styleUrls: ['./new-order.component.scss']
})
export class NewOrderComponent implements OnInit {
  newOrderForm: FormGroup;
  minDate;
  isShowLoader = false;
  months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
  vendorList = [
    { name: "MRF", id: "45323452" },
    { name: "Apollo", id: "12345678" }
  ]
  constructor(private orderService: OrderService, private snackBarService: SnackBarService) { }

  ngOnInit() {
    this.initializeFormValidation();
    this.minDate = new Date();
    // this.minDate.setFullYear(this.maxDate.getFullYear() - 18)
  }
  private initializeFormValidation() {
    this.newOrderForm = new FormGroup({
      // serialNo: new FormControl('', {
      //   validators: [Validators.required]
      // }),
      vendorName: new FormControl('Select a Vendor', { validators: [Validators.required] }),
      vendorCode: new FormControl('', { validators: [Validators.required] }),
      orderCode: new FormControl('Select Order Code', { validators: [Validators.required] }),
      orderDescription: new FormControl('', { validators: [Validators.required] }),
      orderQuantity: new FormControl('', { validators: [Validators.required] }),
      orderRaisedName: new FormControl('Ethan', { validators: [Validators.required] }),
      // order_rasied_department: new FormControl('', { validators: [Validators.required] }),
      expectedTimeDelivery: new FormControl('', { validators: [Validators.required] }),
      deliveryAddress: new FormControl('', { validators: [Validators.required] })
    })
  }
  onChange(value: string) {
    let vendor = this.vendorList.find(ele => {
      return ele.name == value ? ele : ''
    })

    this.newOrderForm.patchValue({ vendorCode: vendor ? vendor.id : '' })
  }
  onSubmitOrder() {
    this.isShowLoader = true
    this.changeDateFormat(this.newOrderForm.value.expectedTimeDelivery);
    let obj = { ... this.newOrderForm.value };
    obj["userId"] = "12345";
    console.log(obj)
    this.orderService.createOrder(obj).subscribe(data => {
      this.isShowLoader = false;
      Swal.fire({
        text: data["message"],
        icon: 'success',
        showCancelButton: false,
        confirmButtonText: 'Yes',
      })
      this.newOrderForm.reset();

      // this.snackBarService.openSnackBar(data["message"], '', "blue-snackbar");
      console.log(data)
    },
      error => {
        Swal.fire({
          text: "Oops! Something went wrong",
          icon: 'warning',
          showCancelButton: false,
          confirmButtonText: 'Yes',
        })
        this.newOrderForm.reset();
        this.isShowLoader = false

      })


  }
  /**
   * 
   * @param dateMat 
   * @description change default date to custom date ("12-March-2020")
   */
  changeDateFormat(dateMat) {
    let date = new Date(dateMat);
    const dd = +date.getDate();
    const mmm = this.months[date.getMonth()];
    const yyyy = +date.getFullYear();
    let etd = `${dd}-${mmm}-${yyyy}`;
    this.newOrderForm.patchValue({ expectedTimeDelivery: etd })

  }

}

