import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { Observable } from 'rxjs';
import { OrderService } from '../../services/order.service';

@Component({
  selector: 'app-order',
  templateUrl: './order.component.html',
  styleUrls: ['./order.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class OrderComponent implements OnInit {
  isSubVendor: boolean = false;
  isTransport: boolean = false;
  isShowError: boolean = false;
  roleType: string;
  selected = "In-Bound Order";
  selectedIndex = 0;
  displayedColumns: string[] = ['order_id', 'name', 'creation_date', 'expected_Time_Delivery', 'status'];
  allOrders;
  ordersFilterData = null;
  public isShowLoader: boolean = false;
  constructor(private orderService: OrderService) {
    let roleType = localStorage.getItem("roleType");
    if (roleType == 'v') {
      this.displayedColumns.push('action');
    }
  }

  ngOnInit() {
    this.isShowLoader = true;
    this.roleType = localStorage.getItem('roleType');
    this.getOrder();
  
    

  }
  private getOrder() {
    let data = {
      userName: "Ethan",
      password: "abc@123"
    }
    this.orderService.getOrders(data).subscribe((data: any) => {
      this.isShowError = false;
      this.isShowLoader = false
      this.allOrders = data
      this.ordersFilterData = [...data];
    }, error => {
      this.isShowError = true
      console.log(error);
      this.isShowLoader = false;
    }
    )
  }
  convertDate(date) {

  }

  onChange(selected) {
    this.selectedIndex = 0;
    let directionName = this.selected.trim().toLowerCase();
    if (directionName === "out-bound order" || directionName.includes("transport")) {
      this.isSubVendor = true;

    } else {
      this.isSubVendor = false
    }
    this.orderService.setDirection(directionName);

  }
  orderTabChange(value: string) {
    this.ordersFilterData = null
    let key: string = value.trim().toLowerCase();
    if (key == "order in progress") {
      key = "in progress"
    }
    if (this.allOrders) {
      this.ordersFilterData = this.allOrders.filter(ele => {

        return key === 'all order' ? this.allOrders : ele.status.toLowerCase().trim() === key;
      })
    }

  }

}
