import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Observable, Subject } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class OrderService {
  private changeDirection = new Subject<any>();

  constructor(private http: HttpClient) { }

  public getOrders(data) {
    const headers = new HttpHeaders();
    headers.set('Content-Type', 'application/json');
    // return this.http.get('assets/orde.json');
    return this.http.get('assets/vendor.json');
    // return this.http.post(`${environment.BASE_URL}getAllOrdersForManufacturer/`, data, { headers: headers });
  }
  public createOrder(orderObj) {
    const headers = new HttpHeaders();
    headers.set('Content-Type', 'application/json');
    return this.http.post(`${environment.BASE_URL}createOrder/`, orderObj, { headers: headers });
  }
  public getDashBoardData(data) {
    const headers = new HttpHeaders();
    headers.set('Content-Type', 'application/json');
    return this.http.post(`${environment.BASE_URL}getDashboardDetails/`, data, { headers: headers });

  }
  setDirection(direction: string) {
    this.changeDirection.next({ directionName: direction });
  }
  getDirection() {
    return this.changeDirection.asObservable();
  }
  

}
