import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DefaultLayoutComponent } from '../containers';
import { NewOrderComponent } from './component/order/new-order/new-order.component';
import { OrderDetailsComponent } from './component/order/order-details/order-details.component';


const routes: Routes = [
    {
        path: '', component: DefaultLayoutComponent,
        children: [
            {
                path: '', redirectTo: 'dashboard', pathMatch: 'full'
            },

            {
                path: 'dashboard',
                loadChildren: () => import('./component/dashboard/dashboard.module').then(m =>
                    m.DashboardModule)
            },
            {
                path: 'orders',
                loadChildren: () => import('./component/order/order.module').then(m =>
                    m.OrderModule)
            },
            {
                path: 'new-order',
                component: NewOrderComponent
            },
            {
                path: 'order-details',
                component: OrderDetailsComponent
            }
        ]
    }

];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class CoreRoutingModule { }
